import React from "react";
import * as Actions from "../actions/Actions";
import Store from "../stores/Store";
import  './jobQueueCount.css'
//import JSONPretty from 'react-json-pretty';
import ReactJson from 'react-json-view';

export default class GetUserIdByVin extends React.Component {
  constructor() {
    super();
    this.getUserIds = this.getUserIds.bind(this);
    this.state = {
      userIds: Store.getUserIds(),
    };
  }

 

  componentWillMount() {
    Store.on("change", this.getUserIds);
  }

  componentWillUnmount() {
    Store.removeListener("change", this.getUserIds);
  }

  getUserIds() {
    this.setState({
      userIds: Store.getUserIds(),
    });
  }

  reloadGetUserByVin = async(e) =>{
    e.preventDefault()
    const send = e.currentTarget.VIN.value
    Actions.reloadGetUserByVin(send);
  }

  generateUserIds(){
    const { userIds } = this.state;
    //console.log(this.state)
    var render = [];
    var i = 0;
    if(typeof userIds !== 'undefined'){
      //render.push(<JSONPretty id="json-pretty" json={userIds}></JSONPretty>)
      render.push(<ReactJson src={userIds} 
        enableClipboard={false} displayDataTypes={false}
        displayObjectSize = {false} key={i}/>)
    }
    return render;


  }
  render() {
    
    return (
      <div>
        <h1>Get User ID By VIN</h1>
        <form onSubmit = {this.reloadGetUserByVin.bind(this)}>
          <input type="text" name="VIN" placeholder="Enter VIN..."/>
          <button>Refresh</button>
        </form>
        <br></br>
        {this.generateUserIds()}
      </div>
    );
  }
}



