import React from "react";
import * as Actions from "../actions/Actions";
import Store from "../stores/Store";
import  './jobQueueCount.css'
//import JSONPretty from 'react-json-pretty';
import ReactJson from 'react-json-view';


export default class JobQueueCount extends React.Component {
  constructor() {
    super();
    this.getJobs = this.getJobs.bind(this);
    this.state = {
      jobs: Store.getJobs(),
    };
  }

 

  componentWillMount() {
    Store.on("change", this.getJobs);
  }

  componentWillUnmount() {
    Store.removeListener("change", this.getJobs);
  }

  getJobs() {
    this.setState({
      jobs: Store.getJobs(),
    });
  }

  reloadGetJobQueueCount() {
    Actions.reloadGetJobQueueCount();
  }

  generateJobList(){
    const { jobs } = this.state;
    var render = []
    var i = 0;
    if(typeof jobs !== 'undefined'){
      //render.push(<JSONPretty id="json-pretty" json={jobs}></JSONPretty>)
      render.push(<ReactJson src={jobs} 
        enableClipboard={false} displayDataTypes={false}
        displayObjectSize = {false} key={i}/>)
    }
    return render;
    
    
  }
  render() {    
    return (
      <div>
        <h1>Get Job Queue Count</h1>
        <button onClick={this.reloadGetJobQueueCount.bind(this)}>Refresh</button>
        {this.generateJobList()}
      </div>
    );
  }
}



