import React from "react";
import * as Actions from "../actions/Actions";
import Store from "../stores/Store";
//import JSONPretty from 'react-json-pretty';
import ReactJson from 'react-json-view';

export default class QueryAccountDetails extends React.Component {
  constructor() {
    super();
    this.getAccountDetails = this.getAccountDetails.bind(this);
    this.state = {
      accountDetails: Store.getAccountDetails(),
    };
  }

  componentWillMount() {
    Store.on("change", this.getAccountDetails);
  }

  componentWillUnmount() {
    Store.removeListener("change", this.getJobs);
  }

  getAccountDetails() {
    this.setState({
      accountDetails: Store.getAccountDetails(),
    });
  }

  reloadQueryAccountDetails = async (e) => {
    e.preventDefault()
    const send = e.currentTarget.VIN.value
    Actions.reloadQueryAccountDetails(send);
  }

  generateAccountList(){
    const { accountDetails } = this.state;
    var render = [];
    var i = 0;
    if(typeof accountDetails !== 'undefined'){
      //render.push(<JSONPretty id="json-pretty" json={accountDetails.Data}></JSONPretty>)
      render.push(<ReactJson src={accountDetails.Data} 
        enableClipboard={false} displayDataTypes={false}
        displayObjectSize = {false} key={i} />)
      i++;
    }
    return render;

  }



  render() {
    return (
      <div>
        <h1>Query Account Details</h1>
        <form onSubmit = {this.reloadQueryAccountDetails.bind(this)}>
          <input type="text" name="VIN" placeholder="Enter VIN..."/>
          <button>Refresh</button>
        </form>
        <br></br>
        {this.generateAccountList()}
      </div>
    );
  }
}



