import { EventEmitter } from "events";

import dispatcher from "../dispatcher";

class Store extends EventEmitter {
  constructor() {
    super()
    this.jobs = undefined
    this.accountDetails = undefined
    this.userIds = undefined
    this.messageResponse = undefined
  }
  
  getJobs() {
    return this.jobs;
  }

  getUserIds(){
    return this.userIds;
  }

  getAccountDetails(){
    return this.accountDetails;
  }

  getMessageResponse(){
    return this.messageResponse;
  }


  handleActions(action) {
    switch(action.type) {
      case "RECEIVE_GET_JOB_QUEUE_COUNT": {
        this.jobs = action.jobs;
        this.emit("change");
        break;
      }case "RECEIVE_QUERY_ACCOUNT_DETAILS":{
        this.accountDetails = action.accountDetails;
        this.emit("change");
        break;
      }case "FAILED_RECEIVE_QUERY_ACCOUNT_DETAILS":{
        this.accountDetails = action.accountDetails;
        this.emit("change");
        break;
      }case "RECEIVE_GET_USER_ID_BY_VIN" :{
        this.userIds = action.userIds;
        this.emit("change");
        break;
      }case "RECEIVE_SEND_SMS_RESPONSE":{
        this.messageResponse = action.messageResponse;
        this.emit("change");
        break;
      }default:{
        break;
      }
    }
  }

}

const store = new Store();
dispatcher.register(store.handleActions.bind(store));

export default store;



