import React from "react";
import * as Actions from "../actions/Actions";
import Store from "../stores/Store";
//import JobList from '../components/JobList'
import  './jobQueueCount.css'
//import JSONPretty from 'react-json-pretty';
import ReactJson from 'react-json-view';

export default class SendSMS extends React.Component {
  constructor() {
    super();
    this.getMessageResponse = this.getMessageResponse.bind(this);
    this.state = {
      response: Store.getMessageResponse(),
    };
  }

  componentWillMount() {
    Store.on("change", this.getMessageResponse);
  }

  componentWillUnmount() {
    Store.removeListener("change", this.getMessageResponse);
  }

  getMessageResponse() {
    this.setState({
      response: Store.getMessageResponse(),
    });
  }

  reloadSendSMS = async(e) =>{
    e.preventDefault()
    const sendPhone = e.currentTarget.PhoneNumber.value
    const sendMessage = e.currentTarget.TextMessage.value
    Actions.reloadSendSMS(sendPhone, sendMessage);
  }


  generateMessageResponse(){
    const response = this.state.response;
    var render = [];
    var i = 0;
    if(typeof response !== 'undefined'){
      //render.push(<JSONPretty id="json-pretty" json={response}></JSONPretty>)
      render.push(<ReactJson src={response} 
        enableClipboard={false} displayDataTypes={false}
        displayObjectSize = {false} key={i}/>)
      i++;
    }
    return render;
  }

  render() {
    
    return (
      <div>
        <h1>Send SMS</h1>
        <form onSubmit = {this.reloadSendSMS.bind(this)}>
          <input type="text" name="PhoneNumber" placeholder="Enter Phone Number..."/>
          <input type="text" name="TextMessage" placeholder="Enter Message..."/>
          <button>Send</button>
        </form>
        <br></br>
        {this.generateMessageResponse()}
      </div>
    );
  }
}



